import blog from '@/api/blog'

window.blog = blog
export default {
    name: 'login',
    data() {
        return {
            blogs: [],
            total: 0,
            page: 1
        }
    },
    created() {
        this.page = parseInt(this.$route.query.page) || 1 //刷新之后仍然在刚刚浏览的页面
        blog.getIndexBlogs({page: this.page}).then(res=> {
            console.log(res)
            this.blogs = res.data
            this.total = res.totalPage
            this.page = res.page
        })
    },
    methods: {
        handleCurrentChange(newPage) {
            console.log(newPage)
            blog.getIndexBlogs({page: newPage}).then(res=> {
                console.log(res)
                this.blogs = res.data
                this.total = res.totalPage
                this.page = res.page
                this.$router.push({path: '/', query: {page: newPage}}) //跳转到到path:'/'，然后后面网址加上page=newPage
            })
        }
    }
}


